@import url(https://fonts.googleapis.com/css?family=Fredoka+One);
.App-logo {
    height: 160px;
    -webkit-filter: drop-shadow(0 0 0.28rem #e7d8786b);
            filter: drop-shadow(0 0 0.28rem #e7d8786b);
    align-items: center;
    justify-content: center;
    text-align: center;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}

.App-logo:hover { -webkit-transform: scale(1.1); transform: scale(1.1); }

.App-logo-rotation {
    -webkit-animation: App-logo-spin infinite 5s linear;
            animation: App-logo-spin infinite 5s linear;
  }
  
@-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
@keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
.button {
    font-size: 16px;
    font-weight: lighter;
    height: 50px;
    border-radius: 30px;
    width: 100%;
    margin: 4px 0px 4px 0px;
}

.box {
    padding: 6px;
    background-color: white;
    border-radius: 6px;
    border-bottom: 16px solid #c84132;
    box-shadow: 0 2px 3px #8a8888;
}
body {
  background-color: #2e2929; /* e74c3c f04d3b  272222 */
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Fredoka One', cursive;
  color: white;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.title {
  font-weight: bold;
}

.App {
  text-align: center;
  color: #131313;
}

.App-header {
  display: flex;
  min-height: 5vh;
  flex-direction: column;
  text-align: 'left';
  align-items: center;
  justify-content: center;
  font-size: 44px;
  color: white;  
  text-shadow:  0 2px 4px #909090; /* 909090 */
  margin-bottom: 6px;
}

a {
  color: #f1fb61;
}

a:hover {
  color: #fddec0;
}

.footer {
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 8px;
  color: #ffffff;
  text-shadow:  0 2px 3px #a3a3a3;
}


.timer {
  font-size: 110px; 
  font-weight: normal;
  text-shadow:  0 2px 3px #8a8888; /*0 0 1px #4b4b4b, 0 0 5px rgb(193, 194, 190);  a3a3a3*/
  color: white;
}
